import { useContext } from "react"
import { DataContext } from "./DataProvider"
import Button from 'react-bootstrap/Button'

function SortIcon({ size, direction, ...rest }) {
    return (
        <svg
            width={ size }
            height={ size }
            fill="currentColor"
            viewBox="0 0 16 16"
            style={ { transformOrigin : "center", transform : `rotate(${direction === 1 ? 180 : 0 }deg)` } }
            { ...rest }
        >
            <path d="M6 10.5a.5.5 0 0 1 .5-.5h3a.5.5 0 0 1 0 1h-3a.5.5 0 0 1-.5-.5zm-2-3a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 0 1h-7a.5.5 0 0 1-.5-.5zm-2-3a.5.5 0 0 1 .5-.5h11a.5.5 0 0 1 0 1h-11a.5.5 0 0 1-.5-.5z"/>
        </svg>
    )
}

SortIcon.defaultProps = {
    size : 16
}

export default function Th({ label, value }) {
    const { data : { orderBy, orderDirection }, data, setData } = useContext(DataContext)

    const handleClick = () => {
        if (value === orderBy) setData({ ...data, orderDirection : orderDirection * -1 })
        else setData({ ...data, orderBy : value, orderDirection : 1 })
    }

    return (
        <th>
            <Button
                onClick={ handleClick }
                style={ { background : "none", color : "inherit", border : "none", fontWeight : "bold" } }
            >
                { label }
                { data.orderBy === value && <>&nbsp;<SortIcon direction={ orderDirection }/></> }
            </Button>
        </th>
    )
}